.word {
  font-size: 3rem;
  -webkit-transition: opacity 400ms;
  -o-transition: opacity 400ms;
  transition: opacity 400ms;
}
.word:not(:last-child) {
  margin-right: 0.5em;
}
.word strong {
  background: rebeccapurple;
}
.word.completed {
  opacity: 0.2;
}



